.btn {
  background-color: #222;
  color: #fff;
  padding: 0.7em 1.2em;
  text-decoration: none;
  transition: 0.5s;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.btn:hover {
  color: #ffbb33;
}

.btn-disabled {
  background-color: grey;
  color: #fff;
  padding: 0.7em 1.2em;
  text-decoration: none;
  transition: 0.5s;
  border: none;
  border-radius: 5px;
}